.bar {
    padding: 2em 0;
    width: 200px;
    background-color: rgb(34, 170, 136);
    height: 77vh;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    height: auto;
    min-height: 75vh;
}

.bar ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.bar h2 {
    margin: 0.5em 0.5em;
    border-bottom: 1px solid black;
}

.new_project {
    background-color: white;
    border: 2px solid black;
    border-radius: 5px;
    width: 300px;
    padding: 1em;

}

.new_project div {
    position:relative;
    justify-content: space-between;
    display: flex;
}

.new_project button {
    flex-basis: 49%;
    padding: 0.5em 0;
    border-radius: 5px;
    color: white;
    border: none;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
}

.red {
    background-color: rgb(206, 105, 113);
}

.green {
    background-color: rgb(34, 170, 136);
}

.new_project_button {
    display: flex;
    align-items: center;
    gap: 0.5em;
    background-color: inherit;
    border: none;
    border-radius: 15px;
    font-size: 1.2em;
    margin-top: 2em;
    margin-left: 1em;
    cursor: pointer;
    transition: .3s;
}

.new_project_button:hover {
    font-weight: bold;
}

.icon {
    font-size: 1.5em;
    transition: 1s;
}

.new_project_button:hover .icon{
    transform: rotate(1turn) scale(1.1);
}



